<template>
    <v-container
        cols="12"
        sm="4"
        md="6">
        <v-row>
            <v-text-field :rules="rules" width="400px"></v-text-field>
        </v-row>
        <TablesData></TablesData>
        <FooterBtn class="footerBtns"></FooterBtn>
    </v-container>
</template>

<script>
import TablesData from './Tables.vue'

import FooterBtn from './FooterButtons'

export default {
    components: {
        TablesData,
        FooterBtn
    },
    data() {
        return {
            value: 0,
            rules: [
                value => !!value || 'Required.',
                value => {
                const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                return pattern.test(value) || 'Invalid e-mail.'
                },
            ],
        }
    },
    methods: {

    },
    computed: {
      color () {
        switch (this.value) {
          case 0: return 'blue-grey'
          case 1: return 'teal'
          case 2: return 'brown'
          case 3: return 'indigo'
          default: return 'blue-grey'
        }
      },
    },
    created() {

    }
}
</script>

