<template>
  <v-bottom-navigation
    v-model="value"
    :background-color="color"
    light
    shift
  >
    <v-btn>
      <span>Расходы</span>
      <v-icon class="material-icons">trending_down</v-icon>
    </v-btn>
    <v-btn>
      <span>Доходы</span>
      <v-icon class="material-icons">trending_up</v-icon>
    </v-btn>
    <v-btn>
      <span>Цели</span>
      <v-icon class="material-icons">my_location</v-icon>
    </v-btn>
    <v-btn>
      <span>Статистика</span>
      <v-icon class="material-icons">bar_chart</v-icon>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
  export default {
    data: () => ({ value: 3 }),

    computed: {
      color () {
        switch (this.value) {
          case 0: return 'blue-grey'
          case 1: return 'teal'
          case 2: return 'brown'
          case 3: return 'indigo'
          default: return 'blue-grey'
        }
      },
    },
  }
</script>

